import rCheckBox from '@/components/common/RHX/rCheckBox/rCheckBox';
import styleSelectionList from './styleSelectionList';
export default {
	components: {
		styleSelectionList,
		rCheckBox
	},
	props: [
		'disabled',
		'itemsData',
		'indexNum',
		'dataList',
		'deleteMoveShow'
	],
	data() {
		return {
			dataLists: this.dataList,
		};
	},
	watch: {
		dataList: {
			handler(a, b) {
				console.log('----------------dataList--------------')
				this.dataLists = a;
			},
			deep: true
		},
	},
	created() {
	},
	methods: {
		// 样式选择
		tapCheckBoxFour(e) {
			console.log(e, '样式选择');
			console.log('用户选择行为,非模板行为！！！！！！！！！！！！！！！！！');
			if (e.length > 0) {
				this.$emit('tap-StyleSelection', e);
			}
		},
		// 编辑触发的用户行为
		tapStyleSelectionList (e) {
			console.log(e, '编辑触发的用户行为');
			this.$emit('tap-StyleSelection', e);
		}
	}
};
